<template>
  <div class="progress">
    <div
      v-for="(value,item,index) in data"
      :key="index"
    >
      <div v-if="item == 'goalItemName'" class="progressName">{{value}}</div>
      <div v-else class="progressItem">
        <div v-if="value" class="number" :class="item=='goalDetail'?'blue':item=='planProgress'?'green':item=='actualProgress'?'red':''" :style="{width: item !== 'goalDetail'?value : '100%'}"></div>
        <div v-if="value && item=='goalDetail'" class="text goal">{{value}}</div>
        <div v-if="value && (item=='planProgress'||item=='actualProgress')" class="text plan">{{(item=='planProgress'?'计划进度':item=='actualProgress'?'实际进度':'')+value}}</div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  name: "GoalProgress",
  data() {
    return {};
  },
  props:{
    data:{
      type:Object
    }
  },
  mounted() {},
  methods: {
    
  },
  components: {}
};
</script>
<style lang="scss" scoped>
.progress{
  padding: 0 5px 0 10px;
  .progressName{
    margin-bottom: 9px;
  }
  .progressItem{
    display: flex;
    margin-bottom: 10px;
    .text{
      line-height: 10px;
      margin-left: 10px;
      color: #777777;
      letter-spacing: -0.41px;
    }
    .goal{
        flex:0 0 60px;
    }
    .plan{
        flex:0 0 85px;
    }
    .number{
      height: 10px;
      border-radius: 5px;
    }
    .blue{
      background: #24A9F0;
    }
    .green{
      background: #48C2C5;
    }
    .red{
     background : #FE6666;
    }
  }
}
</style>



